.navbar {
    height: 4rem;
    text-align: center;
    margin: 0;
    padding: 1 rem 2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: sticky;
    top: 0;
    z-index: 3;
    max-width: 100%;
    background-color: #00CFC5;
    box-shadow: 1px 1px #FF6E1E;
    

}
.navMenu{
    justify-content: center;
    align-items: center;
    width: 100%;
}

.logo{
    object-fit: cover;
    height: 3rem;
    width: 3rem;
}
.listItem{
  margin: 1rem;
  cursor: pointer;  
}
.listItem:hover{
color: #FF6E1E;
padding-bottom: 0.5rem;
border-bottom: 2px solid #F44900;
font-weight: bolder;
font-size: 1rem;

}

.active{
    color: #FF6E1E;
    padding-bottom: 0.5rem;
    border-bottom: 3px solid #F44900;
}
.mobMenu{
    display: none;
    object-fit: cover;
    height: 1.8rem;
    margin-right: 2rem;
    width: 3rem;
    height: 3rem;
}

.dropMenu{
    position: absolute;
    top: 4rem;
    right: 0.7rem;
    z-index: 2;
    flex-direction: column;
    padding: 0.3rem;
    height: fit-content;
    min-width: 5rem;

}
.dropMenu:hover{
    cursor: pointer;
}
.dropItem{
    color: #fff;
    padding: 0.5rem 1rem;
    margin: 0.25rem;
    background: #FF6E1E;
    border-radius: 0.5rem;
}
@media screen and (max-width:720px) 
{
    .mobMenu{
        display: flex;
    }
    .navMenu{
        display: none;
    }
    
}