#skills {
    overflow: hidden;
    width: 100vw;
    max-width: 70rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 0 auto;
}

.title {
    font-size: 3rem;
    font-weight: 600px;
    padding-top: 3rem;
    margin-bottom: 1.5rem;
    color: #FF6E1E;
}

.desc {
    font-weight: 200;
    font-size: 1rem;
    max-width: 70rem;
    padding: 0 2rem;
    text-align: center;
    justify-content: center;
    align-items: center;
    margin-top: -1rem;
}

.sBar {
    margin: 0;
    width: 100vw;
    max-width: 90%;
    text-align: left;
}

.bar {
    display: flex;
    margin: 1rem;
    padding: 0.7rem 0.7rem;
    border-radius: 1rem;
    color: #FF6E1E;
    background-color: #fff;
}

.barImage {
    width: 60px;
    height: 60px;
    object-fit: cover;
    margin-right: 2rem;
}

.barText {
    font-size: 0.8rem;
    font-weight: 200;
}

@media screen and (max-width:720px) {
    .barText>p {
        font-size: 2vw;
    }

}

@media screen and (max-width:480px) {
    .sBar {
        align-items: center;
    }

}