#contactPage{
    min-height: calc(100vh - 4rem);
    width: 100vm;
    max-width: 60rem;
    margin: 0 auto;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.title{
    margin: 1.5rem;
    font-size: 3rem;
    font-weight: 600px;
    color: #FF6E1E;

}

.desc{
padding: 1rem;
font-size: medium;
font-weight: 300;
}
.contactForm{
    margin: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 90vw;
    max-width: 60rem;
    
}
.name, .email, .msg {
    font-size: medium;
    width: 100%;
    max-width: 40rem;
    margin: 0.5rem;
    padding: 0.5rem 1rem;
    color: #fff;
    border: none;
    border-radius: 0.5rem;
    background: #fff;
}

.links{
   
    display: flex;
    flex-wrap: wrap;
    
}
.link{
    object-fit: cover;
    height: 3rem;
    width: 3rem;
    margin: 0 0.5rem;
   
}
.submitBtn{
    margin: 0.5rem;
    height: 2rem;
    cursor: pointer;
    
}