#intro{
    height: calc(100vh - 19rem);
    width: 90vw;
    max-width: 75rem;
    margin: 0 auto;
    overflow: hidden;
    position: relative;
    
}
.myImage{
    position: absolute;
    z-index: -1;
    top: -5rem;
    right: 0;
    object-fit: cover;
    height: 60vh;
    border-radius: 1rem;
    opacity: 1;
    
}
.content{
    height: 70vh;
    width: 100vh;
    padding: 2rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    

}
h1,h2,p,h3{padding: 0.2rem; text-wrap: wrap;}
button{
    width: 7rem;
    height: 2rem;
    background-color: #FF6E1E;
    color: #fff;
    border-radius: 1rem;
    font-weight: bolder;
    
    
}
#pooja{
    color: #FF6E1E;
    text-shadow: 1px 1px #09E6DE;
}

@media screen and (max-width:840px){
   .content{ right: -10vw;} 
}
@media screen and (max-width:480px){
    .content{ width: 360px;} 
} 

.resume{
    margin: 1rem 0;
    color: #fff;
    
}
u:hover{
    cursor: pointer;
    color: #FF6E1E;
}