#works{
    margin: 0 auto;
    min-height: calc(100vh - 4rem);
    width: 100vw;
    max-width: 100rem;
    display: flex;
    align-items: center;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    padding: 0 1rem;
    

}
.title{
    margin: 1.5rem;
    font-size: 3rem;
    font-weight: 600px;
    color: #FF6E1E;
    text-shadow: 1px 1px #09E6DE;
}
.desc{
font-size: 1.5rem;
font-weight: 300;
color: #fff;
}
.wImage{
display: flex;
width: 100vw;
max-width: 65rem;
align-items: center;
justify-content: center;
flex-wrap: wrap;
}
.image{
    object-fit: cover;
    height: 20rem;
    width: 20rem;
    margin: 0.5rem;
    border-radius: 1rem;
   
}

.image:hover{

    rotate: 360deg;
    transition: rotate 1.5s;
   
}

.wLinks{
    display: flex;
    width: 100vw;
    max-width: 65rem;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    
}
.w{
    object-fit: cover;
    height: 4rem;
    width: 20rem;
    margin: 0.5rem;
    border-radius: 1rem;
    padding: 1rem;
    background-color: #FF6E1E;
    font-size: 1.5rem;


}
a{
    color: #fff;
}
